import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable, of } from 'rxjs';
import { catchError, map, tap } from 'rxjs/operators';

import { posts, blogPost } from './posts.module';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class PostServiceService {
  private postUrl = 'api/posts';

  constructor(private http: HttpClient) { }

  getPosts() {
    return of(posts);
  }

  post(id: number) {
    return of(posts.find(post => +post.id === +id));
  }
  addingPost(newPost: blogPost): Observable<blogPost> {
    newPost.id = posts.length + 1;
    posts.push(newPost);
    return this.http.post<blogPost>(this.postUrl, newPost, httpOptions);
    //   .pipe(
    //     tap((newPost: blogPost) => this.log(`added post w/ id=${newPost.id}`)),
    //     catchError(this.handleError<blogPost>('addingPost'))
    // );
  }

  // private handleError<T> (operation = 'operation', result?: T) {
  //   return (error: any): Observable<T> => {

  //     console.error(error); // log to console instead

  //     this.log(`${operation} failed: ${error.message}`);

  //     return of(result as T);
  //   };
  // }
}
